.myModal{
	position:fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	display: none;
	background-color: rgba(0,0,0,0.5);
}
.myModalContent{
	padding:25px;
	background: white;
	border-radius: 16px;
	min-width: 250px;
	color: teal;
	
}

.myModal.active{
	display: flex;
	justify-content: center;
	align-items: center;
}
